.Nav-Bar {
    grid-column: 2;
    margin-top: 2em;
    background-color: cornsilk;
    opacity: 60%;
    margin-left: 20%;
    margin-right: 20%;
    height: min-content;
    width: 7vw;
    min-width: 100px;
    border-radius: 20px;
}

.link {
  margin: 5px;
  text-align: center;
  color: black;
  transition: 1s cubic-bezier(.35,.94,.23,.81);
}

a {
  text-decoration: none;
}
.link:hover {
  transform: scale(130%);
  transition: 0.5s;
}
